.slide::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 150%;
  background: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(rgba(0, 0, 0, 0.7)),
    to(transparent)
  );
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(transparent));
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  background: -moz-linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  background: -o-linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  background: linear-gradient(rgba(0, 0, 0, 0.7), transparent);
  bottom: 0;
  left: 0;
}

.previousButton,
.nextButton {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
       -o-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 10;
  /* background: url('http://svgshare.com/i/41_.svg') no-repeat center center /
    16px; */
  /* background: url('https://svgshare.com/i/ACV.svg') no-repeat center center /
    40px; */
  background: url('/images/button-icon.svg') no-repeat center center / 40px;
  width: 32px;
  height: 32px;
  text-indent: -9999px;
  cursor: pointer;
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  -moz-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
}
.previousButton svg path,
.nextButton svg polygon {
  fill: red !important;
}
@media (max-width: 767px) and (orientation: portrait) {
  .previousButton,
  .nextButton {
    background: url('/images/button-icon.svg') no-repeat center center / 30px;
  }
  .previousButton:hover,
  .nextButton:hover {
    background: url('/images/button-icon.svg') no-repeat center center / 20px;
  }
  .previousButton {
    left: 0.2% !important;
  }
  .nextButton {
    right: 0.2% !important;
  }
}
@media (max-width: 400px) and (orientation: portrait) {
  .previousButton,
  .nextButton {
