  .previousButton:hover,
  .nextButton:hover {
    background: url('/images/button-icon.svg') no-repeat center center / 16px;
  }
  .previousButton {
    left: 0.15% !important;
  }
  .nextButton {
    right: 0.15% !important;
  }
}
