  -moz-transform: rotate(180deg) translateY(-moz-calc(50% + 0px));
   -ms-transform: rotate(180deg) translateY(calc(50% + 0px));
    -o-transform: rotate(180deg) translateY(calc(50% + 0px));
       transform: rotate(180deg) translateY(calc(50% + 0px));
}

.nextButton {
